.App {
  text-align: center;
  min-height: 100vmin;
  font-size: calc(10px + 2vmin);
}

.App-image {
  max-width: 50vmin;
  pointer-events: none;
}

main {
  display:flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

blockquote {
  padding:10vmin;
  margin:0;
  text-align:center;
  border:20vmin solid black;
}

.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.back-image-container {
  border:20vmin dashed black;
}

.bio {
  border:10vmin double black;
  padding:5vmin 5vmin 20vmin 5vmin;
  max-width:100vmin;
  max-height:30vmin;
  overflow:scroll;
  text-align:justify;
}

.edgy {
  border:1px dashed black;
  padding-top:1vmin;
}

h3 {
  margin:0;
  padding:0;
  border:10vmin solid black;
}
.App-events {
  margin:0vmin;
  padding:5vmin;
  /* border:10vmin dotted black; */
}
.Event-item {
  margin:3vmin;
  border: 3vmin double black;
  font-size: calc(4px + 2vmin);
  border-radius:2vmin;
  padding:2vmin;
}
.Event-item:hover, .Event-item:focus , .Event-item:focus-within{
  border: 3vmin dashed black;
}


.Event-date {
  display:flex;
  flex-direction: column;
  text-align:center;
  font-size: calc(10px + 2vmin);
  padding:0 0 2vmin 0;

}
.Event-title {
  display:flex;
  flex-direction: row;
  text-align:center;
  align-items:center;
  justify-content: center;
  padding:2vmin;
  font-weight:bold;
  
}
.Event-text {
  /* border:1px solid black; */
  display:flex;
  flex-direction: column;
  align-items:center;
  text-align:right;
  font-style:italic;
  
}
.App-title {
  margin:10vmin;
  padding:10vmin;
  border: 1px dashed black;
  border-radius:10vmin;
}

.Event-video {
  padding:2vmin;
  border:1vmin inset white;
  border-radius:2vmin;
  border-radius:2vmin;
  margin:1vmin;  
  text-decoration:none;
  font-weight: bold;
  color:white;
  background:black;
}
.Event-video:hover, .Event-video:focus{
  border:1vmin outset black;
  color:black;
  background:white;
}
/* https://dev.to/bravemaster619/simplest-way-to-embed-a-youtube-video-in-your-react-app-3bk2 */
.video-responsive {
  overflow: hidden;
  padding-bottom: 56.25%;
  position: relative;
  height: 0;
}

.video-responsive iframe {
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  position: absolute;
}

.Event-link {
  /* text-decoration:none; */
  color:inherit;
}

.Event-gallery-container{
  display:flex;
  flex-direction:row;
}
.Event-gallery {
  margin:auto;
}
.Event-image {
  max-width:40vmin;
}